import React,{useState} from 'react'

export default function Search() {

        const [search,setSearch] =useState(false)

    return (
        <div className='search'>
            <i className="fal fa-search" onClick={()=>{setSearch(true)}}></i>
              <div className={`search-item ${search?'show':""}`}>
                <div className="input">
                    <input type="text" name="" placeholder='Search here..' id="" />
                    <button><i className="fal fa-search"></i></button>
                </div>
                <i className="fal fa-times close" onClick={()=>{setSearch(false)}} ></i>
              </div>
              <div className={`search-container ${search?'show-c':''}`}  onClick={()=>{setSearch(false)}} ></div>
        </div>
    )
}
