import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import {Context} from '../../App'


function Shop() {

    const [icon,setIcon] = useState({
        shop:true,
        h_sidebar:true,
        n_sidebar:true,
    })
    const {shop,h_sidebar,n_sidebar} = icon


    return (
       <Context.Consumer>
           {({width})=>(
                <li className="dropdown-menu" onMouseOver={width > 800 ? () => { setIcon({...icon,shop:false}) } : ''} onMouseOut={width > 800 ? () => { setIcon({...icon,shop:true}) } : ''} >
                    <div className='icon'>
                <Link to='/shop'>shop</Link>{shop?<i className="fal fa-chevron-down" onClick={()=>{setIcon({...icon,shop:false})}}> </i>:<i className="fal fa-chevron-up" onClick={()=>{setIcon({...icon,shop:true})}}></i>} 
                </div>
                    <div className={`dropdown-item shop ${shop?('d-none'):(width>800?'d-flex':'d-block')}`}>
                        <div className="sidebar">
                            <div className="header">
                            <h3>no sidebar</h3>
                            {n_sidebar?<i className="fal fa-chevron-down" onClick={()=>{setIcon({...icon,n_sidebar:false})}} > </i>:<i className="fal fa-chevron-up" onClick={()=>{setIcon({...icon,n_sidebar:true})}}></i>} 
                            </div>
                            <div className={width>800?'d-block':n_sidebar?'d-none':'d-block'}>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            </div>
                        </div>
                        <div className="sidebar">
                        <div className="header">
                            <h3>has sidebar</h3>
                            {h_sidebar?<i className="fal fa-chevron-down" onClick={()=>{setIcon({...icon,h_sidebar:false})}} > </i>:<i className="fal fa-chevron-up" onClick={()=>{setIcon({...icon,h_sidebar:true})}}></i>} 
                        </div>
                          <ul className={width>800?'d-block':h_sidebar?'d-none':'d-block'}>
                              <li><Link to='/'>3 columns</Link></li>
                              <li><Link to='/'>3 columns</Link></li>
                              <li><Link to='/'>3 columns</Link></li>
                              <li><Link to='/'>3 columns</Link></li>
                              <li><Link to='/'>3 columns</Link></li>
                              <li><Link to='/'>3 columns</Link></li>
                              <li><Link to='/'>3 columns</Link></li>
                              <li><Link to='/'>3 columns</Link></li>

                          </ul>
                        </div>
                        <div className="image">
                            <div className="text">
                                <h4>accessories collection</h4>
                                <button>discover now</button>
                            </div>
                        </div>
                    </div>
                </li>
           )}
       </Context.Consumer>
    )
}

export default Shop
