import React, { useState } from 'react'
import {Link} from 'react-router-dom'
import {Context} from '../../App'

function Page() {

    const [page,setPage] =useState(true)
    return (
       <Context.Consumer>
           {({width})=>(
                <li  className="dropdown-menu"  onMouseOver={width > 800 ? () => { setPage(false) } : ''} onMouseOut={width > 800 ? () => { setPage(true) } : ''} >
                <div className='icon'>
                    <Link to='/Page'>Page</Link>{page?<i className="fal fa-chevron-down" onClick={()=>{setPage(false)}}> </i>:<i className="fal fa-chevron-up" onClick={()=>{setPage(true)}}></i>} 
                   
                </div>
                <ul className={`dropdown-item page ${page?'d-none':'d-block'}`}>
                    <li><Link to='/'>about us</Link></li>
                    <li><Link to='/'>contact us</Link></li>
                    <li><Link to='/'>coming soon</Link></li>
                    <li><Link to='/'>404 not found</Link></li>
                    <li><Link to='/'>shop by brand</Link></li>
                    <li><Link to='/'>feedback</Link></li>
                    <li><Link to='/'>help center</Link></li>
                    <li><Link to='/'>order and shipping</Link></li>
                    <li><Link to='/'>style guide</Link></li>
    
                </ul>
            </li>
           )}
       </Context.Consumer>
    )
}

export default Page
