import './nav.css'

import React, {useState} from 'react'
import {Link} from 'react-router-dom'
import Shop from './shop'
import Product from './Product'
import Page from './Page'
import Blog from './Blog'
import Search from './icons/search'
import Account from './icons/Account'
import Favourite from './icons/Favourite'
import Cart from './icons/Cart'
import {Context} from '../../App'


function Nav() {

   const[icon,setIcon]= useState({
       min:true
   })

    const {min} = icon
    return (
        <Context.Consumer>
            {({width})=>(
                <nav>
                <div className="min-icon">
                    {min?<i className='fas fa-align-justify' onClick={()=>{setIcon({...icon,min:false})}}></i>:<i className='fal fa-times' onClick={()=>{setIcon({...icon,min:true})}}></i>}
                </div>
                <div className="brand">
                    <Link to='/'>unicorn</Link>
                </div>
                <ul className={`nav-r ${width>800?'':min?'d-none':'d-block'}`} >
                    <li className='dropdown-menu'><div className="icon"><Link to='/' >home</Link></div></li>
                    <Shop />
                    <Product />
                    <Page />
                    <Blog />
                </ul>
                <div className="nav-icons">
                    <Search />
                    <Account />
                    <Favourite />
                    <Cart />
                </div>
            </nav>
            )}
        </Context.Consumer>
    )
}

export default Nav
