import React,{ useState} from 'react'

export default function Cart() {

        const [product,setProduct] =useState(
            [
                {
                    image:require('../photos/product.jpg').default,
                    title:'balloon fit jeans',
                    price:299.00,
                    count:1,
                    total:299
                },
                {
                    image:require('../photos/product.jpg').default,
                    title:'balloon 2 jeans',
                    price:299.00,
                    count:1,
                    total:299.00
                },
                {
                    image:require('../photos/product.jpg').default,
                    title:'balloon fit jeans',
                    price:299.00,
                    count:1,
                    total:299
                },
                {
                    image:require('../photos/product.jpg').default,
                    title:'balloon 2 jeans',
                    price:299.00,
                    count:1,
                    total:299.00
                },
                {
                    image:require('../photos/product.jpg').default,
                    title:'balloon fit jeans',
                    price:299.00,
                    count:1,
                    total:299
                },
                {
                    image:require('../photos/product.jpg').default,
                    title:'balloon 2 jeans',
                    price:299.00,
                    count:1,
                    total:299.00
                }
            ]
        )

        const [wishlist,setWishlist] = useState(false)

        const remove = id =>{
            let products = product.filter((item,index)=> index !== id)
            setProduct(products)
        }

        var totalPrice = product.reduce((accumulate,{total})=>{
            return accumulate + total
        },0)


        const CountIncrease = (id)=>{
            let products =[ ...product]
            let Count = products.find(item => item.title === id)
            Count.count= Count.count+1
            Count.total= Count.count * Count.price
           setProduct(products)
        }
        
        const CountDicrease = (id)=>{
            let products =[ ...product]
            let Count = products.find(item => item.title === id)
           if(Count.count > 1){
            Count.count= Count.count-1
            Count.total= Count.count * Count.price
           }
           setProduct(products)
        }
       


    return (
        <div className='favourite'>
            <i className="fal fa-shopping-basket" onClick={()=>{setWishlist(true)}}></i>
            <div className={`transparent ${wishlist?'tr-show':''}`} onClick={()=>{setWishlist(false)}}></div>
            <div className={`favourite-body ${wishlist?'fav-show':''}`}>
                <div className="header">
                <h3>shopping cart</h3>
                <i className="fal fa-times" onClick={()=>{setWishlist(false)}}></i>
                </div>
               <div className="item">
               <div className="item-body">
               {product && product.map((item,index) => <Products product={item} key={index} id={index} Remove={remove} increase={CountIncrease} decrease={CountDicrease} />)}
               {product.length !== 0 && (
                    <div className="bottom">
                    <div className="total">
                         <p>Subtotal</p>
                         <p>{`$${totalPrice}`}</p>
                     </div>
                      <div className="btn">
                      <button>checkout</button>
                    <button>view Cart</button>
                      </div>
                    </div>
               )}
               </div>
                {product.length ===0 && <p>you have no item in wishlist !</p>}
               </div>
             
            </div>
        </div>
    )
}

export function Products({product,id,Remove,increase,decrease}){
    return(
        <div className="c-body" id={id}>
            <div className="image">
                <img src={product.image} alt="" />
            </div>
            <div className="text">
            <p>{product.title}</p>
            <div className="btn">
                <button onClick={()=>{decrease(product.title)}}> -</button>
                <p>{product.count}</p>
                <button onClick={()=>{increase(product.title)}}>+</button>
            </div>
            </div>
            <div className="right">
                <i className="fal fa-times" onClick={()=>{Remove(id)}}></i>
                <p>{`$${product.price}`}</p>
            </div>
        </div>
    )
}

