import React,{useEffect, useState} from 'react'

export default function Favourite() {

        const [product,setProduct] =useState(
            [
                {
                    image:require('../photos/product.jpg').default,
                    title:'balloon fit jeans',
                    price:'$299.00'
                },
                {
                    image:require('../photos/product.jpg').default,
                    title:'balloon 2 jeans',
                    price:'$299.00'
                }
            ]
        )

        const [wishlist,setWishlist] = useState(false)

        const remove = id =>{
            let products = product.filter((item,index)=> index !== id)
            setProduct(products)
        }
       


    return (
        <div className='favourite'>
            <i className="fal fa-heart" onClick={()=>{setWishlist(true)}}></i>
            <div className={`transparent ${wishlist?'tr-show':''}`} onClick={()=>{setWishlist(false)}}></div>
            <div className={`favourite-body ${wishlist?'fav-show':''}`}>
                <h3>wishlist products</h3>
                {product && product.map((item,index) => <Products product={item} key={index} id={index} Remove={remove} />)}
                {product.length ===0 && <p>you have no item in wishlist !</p>}
            </div>
        </div>
    )
}

export function Products({product,id,Remove}){
    return(
        <div className="p-body" id={id}>
            <div className="image">
                <img src={product.image} alt="" />
            </div>
            <div className="text">
            <p>{product.title}</p>
            <p>{product.price}</p>
            <button>Select options</button>
            </div>
            <i className="fal fa-times" onClick={()=>{Remove(id)}}></i>
        </div>
    )
}

