import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Context } from '../../App'

function Blog() {
    const [blog, setBlog] = useState(true)
    return (
        <Context.Consumer>
            {({ width }) => (
                <li className="dropdown-menu" onMouseOver={width > 800 ? () => { setBlog(false) } : ''} onMouseOut={width > 800 ? () => { setBlog(true) } : ''}>
                    <div className='icon' >
                        <Link to='/blog'>blog</Link>{blog ? <i className="fal fa-chevron-down" onClick={() => { setBlog(false) }}> </i> : <i className="fal fa-chevron-up" onClick={() => { setBlog(true) }}></i>}
                    </div>
                    <div className={`dropdown-item blog ${blog ? 'd-none' : width>800 ? 'd-flex':'d-block'}`}>
                        <ul >
                            <li><Link to='/'>lusion blog</Link> </li>
                            <li><Link to='/'>blog list style 1</Link> </li>
                            <li><Link to='/'>blog list style 2</Link> </li>
                            <li><Link to='/'>blog list style 3</Link> </li>
                            <li><Link to='/'>blog list style 4</Link> </li>
                            <li><Link to='/'>blog grid style 1</Link> </li>
                            <li><Link to='/'>blog grid style 2</Link> </li>
                        </ul>
                        <ul>
                            <li><Link to='/'> blog grid 3 columns</Link> </li>
                            <li><Link to='/'>blog grid 4 columns</Link> </li>
                            <li><Link to='/'>blog masonry style 1</Link> </li>
                            <li><Link to='/'>blog masonry style 2</Link> </li>
                            <li><Link to='/'>single post style 1</Link> </li>
                            <li><Link to='/'>single post style 2</Link> </li>

                        </ul>
                        <div className="image">
                            <div className="img"></div>
                            <h3>the new assential to alwayes have on hand</h3>
                        </div>
                    </div>
                </li>
            )}
        </Context.Consumer>
    )
}

export default Blog
