import React, { useState } from 'react'

function Account() {

    const [login, setLogin] = useState({
        signin: true,
        register: false,
        forgetPass: false,
    })
    const [modal,setModal] = useState(false)

    const { signin, register, forgetPass } = login

    const handleLogin = () => {
        if (signin === true) return < Login Forgot={handleForgot} />
        if (register === true) return <Register />
    }

    function handleForgot() {
        setLogin({...login, forgetPass: true })
    }

    function handleBack(){
        setLogin({...login,forgetPass:false})
    }

    function handleRegister(){
        setLogin({signin:false,register:true,forgetPass:false})
    }

    return (
        <div className='account'>
            <i className="fal fa-user " onClick={()=>{setModal(true)}}></i>
            <div className={`transparent ${modal?'tr-show':''}`} onClick={()=>{setModal(false)}}></div>
            <div className={`modal ${modal?'modal-open':''}`}>
                <i className="fal fa-times close" onClick={()=>{setModal(false)}}></i>
                {forgetPass ? (<ForgetPass Register={handleRegister} Back={handleBack} />) : (
                    <>
                        <div className="modal-header">
                            <h3 className={signin ? '' : 'text-muted'} onClick={() => { setLogin({ signin: true, register: false, forgetPass: false }) }} >login</h3>
                            <p>|</p>
                            <h3 className={signin ? 'text-muted' : ''} onClick={() => { setLogin({ signin: false, register: true, forgetPass: false }) }} >register</h3>
                        </div>
                        <div className="modal-body">
                            {handleLogin()}
                        </div>
                    </>
                )}
            </div>
        </div>
    )
}

export default Account


export function Login({ Forgot }) {

    return (
        <div className="login">
            <input type="email" placeholder='Email address*' />
            <input type="password" placeholder='password*' />
            <p onClick={() => { Forgot() }}>Forget your password?</p>
            <button>log in</button>

        </div>
    )
}

export function Register() {
    return (
        <div className="register">
            <input type="text" placeholder='first name' />
            <input type="text" placeholder='last name' />
            <input type="email" placeholder='Email address' />
            <input type="password" placeholder='password' />
            <button>Register</button>
        </div>
    )
}

export function ForgetPass({ Back,Register }) {
    return(
        <form>
            <div className="forgetPass">
        <h2>forget your password?</h2>
        <p>we will send you a emaill to reset your password.</p>
        <input type="email" placeholder='Enter your email' />
        <div className="btn-group">
            <button>submit</button>
            <button onClick={()=>{Back()}}>cancel</button>
        </div>
        <button onClick={()=>{Register()}} >Register</button>
    </div>
        </form>
    )
}