import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import {Context} from '../../App'


function Product() {

    const [icon,setIcon] = useState({
        Product:true,
        p_types:true,
        P_styles:true,
        P_details:true
    })
    const {Product,p_types,P_styles,P_details} = icon


    return (
       <Context.Consumer>
           {({width})=>(
                <li className="dropdown-menu" onMouseOver={width > 800 ? () => { setIcon({...icon,Product:false}) } : ''} onMouseOut={width > 800 ? () => { setIcon({...icon,Product:true}) } : ''} >
                    <div className='icon'>
                <Link to='/Product'>Product</Link>{Product?<i className="fal fa-chevron-down" onClick={()=>{setIcon({...icon,Product:false})}}> </i>:<i className="fal fa-chevron-up" onClick={()=>{setIcon({...icon,Product:true})}}></i>} 
                </div>
                    <div className={`dropdown-item product ${Product?'d-none':width>800 ? 'd-flex':'d-block'}`}>
                        <div className="p-styles">
                            <div className="header">
                            <h3>product styles</h3>
                            {P_styles?<i className="fal fa-chevron-down" onClick={()=>{setIcon({...icon,P_styles:false})}} > </i>:<i className="fal fa-chevron-up" onClick={()=>{setIcon({...icon,P_styles:true})}}></i>} 
                            </div>
                           <div className={width>800?'d-block':P_styles?'d-none':'d-block'}>
                                <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                           </div>
                        </div>
                        <div className="p-types">
                        <div className="header">
                            <h3>product types</h3>
                            {p_types?<i className="fal fa-chevron-down" onClick={()=>{setIcon({...icon,p_types:false})}} > </i>:<i className="fal fa-chevron-up" onClick={()=>{setIcon({...icon,p_types:true})}}></i>} 
                        </div>
                        <div className={width>800?'d-block':p_types?'d-none':'d-block'}>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                        </div>
                           

                        </div>
                        <div className="p-details">
                        <div className="header">
                            <h3>product details</h3>
                            {P_details?<i className="fal fa-chevron-down" onClick={()=>{setIcon({...icon,P_details:false})}} > </i>:<i className="fal fa-chevron-up" onClick={()=>{setIcon({...icon,P_details:true})}}></i>} 
                        </div>
                        <div className={width>800?'d-block':P_details?'d-none':'d-block'}>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                            <Link to='/'>3 columns</Link>
                        </div>
                            

                        </div>
                        <div className="image">
                            <div className="text">
                                <h4>accessories collection</h4>
                                <button>discover now</button>
                            </div>
                        </div>
                    </div>
                </li>
           )}
       </Context.Consumer>
    )
}

export default Product
